<template>
  <div>
    <el-row>
      <el-col :span="5">
        <div style="height: 700px; border-right: #999999 solid 1px">
          <div align="center" style="margin-top: 10%; font-size: x-large; font-weight: bold">
            题目信息
          </div>
          <!--          <div class="problem-level info">-->
          <!--            题目难度：{{ level }}-->
          <!--          </div>-->
          <hr class="hr-line">
          <!--          <div class="info">-->
          <!--            题号：{{ problem_ID }}-->
          <!--          </div>-->
          <div class="info">
            {{ problem_info }}
          </div>
        </div>
      </el-col>
      <el-col :span="14">
        <div class="mid">
          <div style="font-size: xx-large; margin-left: 6%">
            距离比赛开始还有
          </div>
          <div style="font-size: xxx-large; color: #999999">
            剩余时间：{{ `${hr}:${min}:${sec}` }}
          </div>
          <div style="margin-left: 18%">
            <el-button @click="dothings">
              <div v-show="isin===1">
                <div v-show="start===1">
                  开始答题
                </div>
                <div v-show="start===0">
                  退出房间
                </div>
              </div>
              <div v-show="isin===0">
                加入房间
              </div>
            </el-button>
          </div>
        </div>
      </el-col>
      <el-col :span="5">
        <div class="right-part">
          <div align="center" class="right-title">
            参与列表
          </div>
          <div class="subtitle">
            参与人数：{{ participants }}
          </div>
          <hr class="hr-line">
          <div v-for="item in participant_list" :key="item" align="center">
            {{ item.toLowerCase() }}
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'room',
  data() {
    return {
      level: 4,
      userid: localStorage.getItem('userid'),
      roomid: localStorage.getItem('roomid'),
      problem_ID: localStorage.getItem('problemid'),
      problem_info: 'here is problem information',
      time_lefth: 22,
      time_leftm: 0,
      participants: 150,
      participant_list: [
        '1',
        '12',
        '123',
      ],
      timenow: localStorage.getItem('starttime'),
      day: 0,
      hr: 0,
      min: 0,
      sec: 0,
      start: 0,
      isin: 0
    }
  },
  mounted() {
    this.countdown()
    this.getparticipants()
  },
  methods: {
    dothings(){
      if(!this.isin) {
        this.joinin()
        this.isin=1
      }
      else if(this.start) {
        this.startgame()
        localStorage.setItem('ispractice', '0')
      }
      else {
        this.quit()
        this.isin=0
      }
    },
    joinin(){
      this.$axios
          .post("/roomMember/join", {
            roomId: this.roomid,
            accountID: this.userid
          })
          .then((res) => {
            alert(res.data.msg);
          })
          .catch((error) => {
            alert("服务器错误roomlist");
            console.log(error);
          });
      this.getparticipants()
    },
    quit(){
      this.$axios
          .post("/roomMember/quit", {
            roomId: this.roomid,
            accountID: this.userid
          })
          .then((res) => {
            alert(res.data.msg);
          })
          .catch((error) => {
            alert("服务器错误roomlist");
            console.log(error);
          });
      this.getparticipants()
    },
    startgame(){
      localStorage.setItem('problemid', this.problem_ID)
      this.$router.push("/question")
    },
    getparticipants(){
        this.$axios
          .post("/roomMember/room/message", {
            roomId: this.roomid
          })
          .then((res) => {
            // alert(res.data.msg);
            if (res.data.code === 0) {
              this.participants=res.data.data.memberCount
              this.participant_list=res.data.data.accountsName
              for(let i in this.participant_list){
                // alert(this.participant_list[i])
                if(this.participant_list[i]===localStorage.getItem('username')){
                  // alert('已经在房间啦')
                  this.isin=1
                }
              }
            }
          })
          .catch((error) => {
            alert("服务器错误roomlist");
            console.log(error);
          });
    },
    countdown: function () {
      const now = Date.parse(new Date())
      const end = Date.parse(new Date(this.timenow))
      const msec = end - now
      if (msec < 0) {
        this.day = 0;
        this.hr = '00';
        this.min = '00';
        this.sec = '00';
        this.start = 1;
      } else {
        let day = parseInt(msec / 1000 / 60 / 60 / 24)
        let hr = parseInt(msec / 1000 / 60 / 60)
        let min = parseInt(msec / 1000 / 60 % 60)
        let sec = parseInt(msec / 1000 % 60)
        this.day = day
        this.hr = hr > 9 ? hr : '0' + hr
        this.min = min > 9 ? min : '0' + min
        this.sec = sec > 9 ? sec : '0' + sec
      }
      const that = this
      setTimeout(function () {
        that.countdown()
      }, 1000)
    },
  }
};
</script>

<style>

.hr-line {
  margin-left: 10%;
  width: 80%;
}

.subtitle {
  color: #999999;
  margin-left: 10%;
}

.right-title {
  margin-top: 10%;
  font-size: x-large;
  font-weight: bold;
}

.right-part {
  margin-top: 10%;
  border-left: #999999 solid 1px;
  height: 700px;
}

.mid {
  margin-top: 30%;
  margin-left: 33%;
}

.info {
  margin-left: 10%;
}

.problem-level {
  color: #999999;
}

.bg-purple {
  background-color: blueviolet;
}

</style>